
#header{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	background: 100%;
	height: 47rem;
	
	background: url(bg.jpg) no-repeat 0/cover,
				url(avator1.png) no-repeat 0/contain;
	background-position: center,bottom;
}
.header-avator{
	height: 26rem;
	width: 26rem;
	padding: 1.6rem;
	margin: 2rem auto;
	border-radius: 100%;
	background: url(avator2.png) no-repeat 0/cover,rgba(255,255,255,.45);
	background-origin: content-box , padding-box ;
}



