
#login{
	margin-top: 5rem;
}
::-webkit-input-placeholder{
	color: black;
}
.login-input-row{
	width: 80%;
	height: 10rem;
	line-height: 10rem;
	margin: 0rem auto;
	border-bottom: 2px solid #ccc;
	background: #fff;
}
.login-input{
	width: 100%;
	height: 10rem;
	line-height: 10rem;
	padding-left: .5rem;
	border: none;	
	letter-spacing: .5rem;
	color: black;
	outline: none;
}
.login-password{
	letter-spacing: 2rem;
}
.login-button-row{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	margin-top: 10rem;
	width: 100%;
	height: 15rem;
}
.login-btn{
	display: block;
	width: 15rem;
	height: 100%;
	padding: .4rem;
	border: .3rem solid #E60012;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border-radius: 100%;
	outline: none;
	background: url(login-icon.png) no-repeat center 0/cover content-box;
}
.login-forgetpas-row{
	margin-top: 3rem;
	width: 100%;
	height: 3rem;
}
.login-forgetpas-row a{
	display: block;
	float: right;
	margin-right: 10%;
	letter-spacing: .2rem;
}
input:-webkit-autofill{
	-webkit-box-shadow:0 0 0 1000px white inset;
}



