
#home{
	position: relative;
	font-size: 3rem;
	color: #595758;
}
.home-username{
	position: absolute;
	width: 100%;
	top: -13rem;
	text-align: center;
	letter-spacing: .1rem;
	font: 3rem verdana;
	color: #fff;
}
.home-infocenter{
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	width: 93%;
	height: 10rem;
	line-height: 10rem;
	margin: 6rem auto 2rem auto;
	border-radius: 1rem;
	border: .4rem solid red;
}
.home-infocenter span:first-child{
	font-size: 4rem;
	letter-spacing: .1rem;
}
.home-Newsnum{
	position: absolute;
	right: 6rem;
	height: 4rem;
	width: 4rem;
	line-height: 4rem;
	border-radius: 50%;
	background: red;
	color: #fff;
	font-size: 3.5rem;
}
.home-fun{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	width: 90%;
	margin: 2rem auto;
	font-size: 3rem;
	border: 1px solid rgba(0,0,0,0);
}
.home-fun > div{
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	height: 10rem;
	border: 1px solid rgba(255,255,255,0);
}
.home-fun > div::after{
	position: absolute;
	content: '';
	top: 0;
	width: 100%;
	height: 100%;
	border-radius: 2rem;
	-webkit-transform: scaleY(1.2) perspective(1000px) rotateX(-28deg);
	        transform: scaleY(1.2) perspective(1000px) rotateX(-28deg);
	-webkit-transform-origin: top;
	        transform-origin: top;
	background: url(function-bg.svg) no-repeat 0/contain;
	-webkit-filter: drop-shadow(0rem -0.5rem .5rem rgba(0,0,0,.5));
	        filter: drop-shadow(0rem -0.5rem .5rem rgba(0,0,0,.5));
}
.home-fun > div:nth-of-type(4)::after{
	background: url(function-bg1.svg) no-repeat 0/contain;
}
.home-fun-notes{
	width: 100%;
}
.home-fun-icon{
	width: 10rem;
	height: 4rem;
	z-index: 9;
}
.home-fun-notes-icon{
	background: url(notes-icon.svg) no-repeat right 0/contain;
}
.home-fun-font{
	margin-left: 10rem;
	height: 8rem;
	width: 12rem;
	text-align: left;
	line-height: 8rem;
	color: #595758;
	letter-spacing: .5rem;
	z-index: 9;
}
.home-fun-contacts{
	top: -2rem;
	width: 98%;
}
.home-fun-contacts-icon{
	background: url(contacts-icon.svg) no-repeat right 0/contain;
}
.home-fun-business{
	top: -4rem;
	width: 96%;
}
.home-fun-business-icon{
	background: url(business-icon.svg) no-repeat right 0/contain;
}
.home-fun-my{
	top: -6rem;
	width: 94%;
}
.home-fun-my-icon{
	background: url(my-icon.svg) no-repeat right 0/contain;
}



